// =============================================================================
// HELLO WORLD TEST ENDPOINT
// =============================================================================
// A minimal protected endpoint for end-to-end testing
// Returns 402 Payment Required with multi-chain CAIP payment details

import { NextRequest, NextResponse } from 'next/server';
import { create402Response } from '@/integrations/x402/nextjs-adapter';
import { getX402ResourceServer } from '@/integrations/x402/resource-server';
import { CAIP_NETWORKS, CAIP_ASSETS } from '@/integrations/x402/caip-utils';
import { getChainRegistry } from '@/integrations/x402/chain-registry';

/**
 * GET /api/test/hello-world
 * 
 * A minimal protected endpoint for end-to-end testing.
 * 
 * Returns:
 * - 402 Payment Required with multi-chain payment details (CAIP format)
 * - 200 OK with greeting after payment verification
 * 
 * Headers:
 * - x-payment: <x402_payment_header> (required for 200 response)
 */
export async function GET(request: NextRequest) {
  // Initialize resource server (registers Bazaar extension)
  // May fail gracefully if x402 packages are not available
  try {
    await getX402ResourceServer();
  } catch (error) {
    // x402 packages not available - continue with basic endpoint
  }

  // Check for payment header
  const paymentHeader = request.headers.get('x-payment') || request.headers.get('X-Payment');

  if (!paymentHeader) {
    // Return 402 with multi-chain payment details using CAIP identifiers
    const chainRegistry = getChainRegistry();
    
    // Build payment details for multiple chains
    const paymentDetails = {
      scheme: 'exact',
      networks: [
        CAIP_NETWORKS.BASE,
        CAIP_NETWORKS.POLYGON,
        CAIP_NETWORKS.ARBITRUM,
      ],
      assets: [
        CAIP_ASSETS.USDC_BASE,
        CAIP_ASSETS.USDC_POLYGON,
        CAIP_ASSETS.USDC_ARBITRUM,
      ],
      amount: '1000000', // 1 USDC (6 decimals)
      recipient: process.env.X402_RECIPIENT_ADDRESS || '0x0000000000000000000000000000000000000000',
      description: 'Hello World Test Endpoint',
      resource: new URL(request.url).toString(),
    };

    // Get network metadata for each supported chain
    const networkDetails = paymentDetails.networks.map((caip) => {
      const network = chainRegistry.get(caip);
      return {
        caip,
        name: network?.name || 'unknown',
        chainId: network?.chainId || null,
        displayName: network?.displayName || 'Unknown',
        nativeCurrency: network?.nativeCurrency,
        rpcUrls: network?.rpcUrls || [],
        blockExplorers: network?.blockExplorers || [],
      };
    });

    // Return 402 with comprehensive payment details
    return NextResponse.json(
      {
        error: 'Payment Required',
        code: 'PAYMENT_REQUIRED',
        message: 'This endpoint requires x402 payment verification',
        paymentDetails: {
          ...paymentDetails,
          networks: networkDetails,
          // Legacy format for backward compatibility
          legacy: {
            network: 'base', // Default
            chainId: 8453,
            token: '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913', // USDC on Base
            tokenSymbol: 'USDC',
          },
        },
        instructions: {
          step1: 'Parse paymentDetails to see available networks and assets',
          step2: 'Select a network/asset combination (e.g., Base + USDC)',
          step3: 'Create an x402 payment authorization for the selected network',
          step4: 'Include payment in x-payment header and retry request',
          step5: 'Receive 200 OK with greeting after verification',
        },
        x402Version: '2.0',
        bazaarCompatible: true,
      },
      {
        status: 402,
        headers: {
          'Content-Type': 'application/json',
          'X-x402-Version': '2.0',
          'X-x402-Scheme': 'exact',
        },
      }
    );
  }

  // Payment header provided - verify and respond
  try {
    // Import verification utilities
    const { parseAndVerifyPaymentHeader } = await import('@/integrations/x402/payment-header-parser');
    const { getMetaFacilitator } = await import('@/integrations/x402/orchestrator/meta-facilitator');
    const baseFacilitatorModule = await import('@/integrations/x402/facilitators/base-facilitator');
    type PaymentRequirements = baseFacilitatorModule.PaymentRequirements;

    // Parse payment header
    const parsed = await parseAndVerifyPaymentHeader(paymentHeader);
    
    if (!parsed.valid || !parsed.payment) {
      return NextResponse.json(
        {
          error: 'Invalid payment',
          code: 'INVALID_PAYMENT',
          details: parsed.error,
        },
        { status: 400 }
      );
    }

    // Build payment requirements
    const baseUrl = process.env.NEXT_PUBLIC_APP_URL || 
                   (request.headers.get('x-forwarded-proto') === 'https' ? 'https' : 'http') + 
                   '://' + (request.headers.get('host') || 'localhost:3001');
    const resourceUrl = `${baseUrl}/api/test/hello-world`;

    const paymentRequirements: PaymentRequirements = {
      scheme: 'exact',
      network: parsed.payment.network || 'base',
      networks: [CAIP_NETWORKS.BASE], // Support CAIP
      maxAmountRequired: '1000000', // 1 USDC
      resource: resourceUrl,
      description: 'Hello World Test Endpoint',
      mimeType: 'application/json',
      payTo: process.env.X402_RECIPIENT_ADDRESS || '0x0000000000000000000000000000000000000000',
      maxTimeoutSeconds: 300,
      asset: '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913', // USDC on Base
      assets: [CAIP_ASSETS.USDC_BASE], // Support CAIP
    };

    // Verify payment using meta-facilitator
    const metaFacilitator = getMetaFacilitator();
    const verifyResult = await metaFacilitator.verifyPayment(
      paymentHeader,
      paymentRequirements,
      {
        requireCompliance: true,
      },
      {
        requireHealthCheck: true,
      }
    );

    if (!verifyResult.valid || !verifyResult.success) {
      return NextResponse.json(
        {
          error: 'Payment verification failed',
          code: 'VERIFICATION_FAILED',
          details: verifyResult.error,
          kytStatus: verifyResult.kytStatus,
          ofacStatus: verifyResult.ofacStatus,
        },
        { status: 400 }
      );
    }

    // Check compliance
    if (verifyResult.kytStatus === 'blocked' || verifyResult.ofacStatus === 'blocked') {
      return NextResponse.json(
        {
          error: 'Payment blocked by compliance check',
          code: 'COMPLIANCE_BLOCKED',
          kytStatus: verifyResult.kytStatus,
          ofacStatus: verifyResult.ofacStatus,
        },
        { status: 403 }
      );
    }

    // Payment verified - return greeting
    return NextResponse.json(
      {
        success: true,
        message: 'Hello, World!',
        greeting: 'Welcome to NexFlow x402 Payment Infrastructure',
        payment: {
          verified: true,
          transactionHash: verifyResult.transactionHash,
          facilitator: verifyResult.facilitatorUsed,
          network: parsed.payment.network,
          amount: parsed.payment.authorization.value,
          kytStatus: verifyResult.kytStatus,
          ofacStatus: verifyResult.ofacStatus,
        },
        timestamp: new Date().toISOString(),
      },
      {
        status: 200,
        headers: {
          'X-x402-Verified': 'true',
          'X-x402-TxHash': verifyResult.transactionHash || '',
          'X-x402-Facilitator': verifyResult.facilitatorUsed || 'cdp',
        },
      }
    );
  } catch (error) {
    return NextResponse.json(
      {
        error: 'Internal server error',
        code: 'INTERNAL_ERROR',
        details: error instanceof Error ? error.message : 'Unknown error',
      },
      { status: 500 }
    );
  }
}

